import styles from '../styles/clean.module.css';

export default function StatusBadge({ status }: { status: string }) {
  const statusMap = {
    pass: { class: styles.statusPass, label: 'Pass' },
    medium: { class: styles.statusMedium, label: 'Medium' },
    fail: { class: styles.statusFail, label: 'Fail' }
  };

  return (
    <span className={`${styles.status} ${statusMap[status as keyof typeof statusMap].class}`}>
      {statusMap[status as keyof typeof statusMap].label}
    </span>
  );
}